import React, { useState } from 'react';
import { useStoreActions } from 'easy-peasy';

const Item = ({ item }) => {
  const { saveItem, deleteItem } = useStoreActions((actions) => ({
    saveItem: actions.saveItem,
    deleteItem: actions.deleteItem,
  }));
  const [state, setState] = useState(item);

  const onChangeFn = (e) => {
    switch (e.target.name) {
      case 'keywords':
        setState({ ...state, [e.target.name]: e.target.value.toLowerCase() });
        break;
      case 'symbol':
        setState({ ...state, [e.target.name]: e.target.value.toUpperCase() });
        break;
      case 'price':
      case 'qty':
        setState({ ...state, [e.target.name]: e.target.value * 1 });
        break;
      default:
        setState({ ...state, [e.target.name]: e.target.value });
        break;
    }
  };

  return (
    <div className='item'>
      <div className='row'>
        <div className='item-field'>
          <label htmlFor='tuname'>Twitter Username</label>
          <input
            type='text'
            name='tuname'
            placeholder='Twitter Username...'
            value={state.tuname}
            onChange={onChangeFn}
            disabled={state.id_str}
          ></input>
        </div>
        <div className='item-field item-keywords'>
          <label htmlFor='keywords'>Keywords</label>
          <input
            type='text'
            name='keywords'
            placeholder='Enter Keywords...'
            value={state.keywords}
            onChange={onChangeFn}
          ></input>
        </div>
      </div>
      <div className='row'>
        <div className='item-field'>
          <label htmlFor='symbol'>Symbol</label>
          <input
            type='text'
            name='symbol'
            placeholder='Symbol...'
            value={state.symbol}
            onChange={onChangeFn}
          ></input>
        </div>
        <div className='item-field'>
          <label htmlFor='side'>Side</label>
          <select name='side' value={state.side} onChange={onChangeFn}>
            {['BUY', 'SELL'].map((side) => (
              <option
                value={side}
                key={side}
                defaultValue={side === state.side}
              >
                {side}
              </option>
            ))}
          </select>
        </div>
        <div className='item-field'>
          <label htmlFor='otype'>Order Type</label>
          <select name='otype' value={state.otype} onChange={onChangeFn}>
            {['MARKET'].map((otype) => (
              <option
                value={otype}
                key={otype}
                defaultValue={otype === state.otype}
              >
                {otype}
              </option>
            ))}
          </select>
        </div>
        <div
          className={
            state.otype === 'MARKET' ? 'item-field item-hide' : 'item-field'
          }
        >
          <label htmlFor='price'>Price %</label>
          <input
            type='number'
            name='price'
            placeholder='Price...'
            value={state.price}
            onChange={onChangeFn}
          ></input>
        </div>
        <div className='item-field'>
          <label htmlFor='qty'>Quantity</label>
          <input
            type='number'
            name='qty'
            placeholder='Quantity...'
            value={state.qty}
            onChange={onChangeFn}
          ></input>
        </div>
      </div>
      <div className='row'>
        <div className='item-field item-logs'>
          <label>Logs</label>
          {state.logs.map((log) => (
            <p key={log?.message.slice(0, 10)}>
              <span
                className={
                  log?.status === 'success' ? 'text-success' : 'text-danger'
                }
              >
                {log?.message + `, on ${new Date(log?.ts).toLocaleString()}`}
              </span>
            </p>
          ))}
        </div>
        <div className='item-field item-buttons'>
          <button
            type='submit'
            className='btn-save'
            disabled={state.status !== 'OPEN'}
            onClick={() => saveItem({ ...state })}
          >
            SAVE
          </button>
          <button
            type='submit'
            className='btn-delete'
            onClick={() => deleteItem(state.id)}
          >
            DELETE
          </button>
        </div>
      </div>
    </div>
  );
};

export default Item;
