import React from 'react';
import { useStoreState } from 'easy-peasy';
import Item from './Item';

const Items = () => {
  const items = useStoreState((state) => state.items);

  return (
    <section className='items'>
      {items.map((item) => (
        <Item item={item} key={item.id}></Item>
      ))}
    </section>
  );
};

export default Items;
