import { action, thunk } from 'easy-peasy';
import toastConfig from './toastConfig';
import { toast } from 'react-toastify';
import swal from 'sweetalert';
import { v4 as uuidv4 } from 'uuid';

const initRow = {
  id: uuidv4(),
  tuname: '',
  keywords: '',
  symbol: '',
  side: 'BUY',
  otype: 'MARKET',
  qty: 0,
  price: 0,
  logs: [],
  status: 'OPEN', //OPEN/CLOSE
  id_str: '',
};

export default {
  //state
  items: [],
  //thunk
  getItems: thunk(async (actions) => {
    try {
      const url = '/items';
      const res = await fetch(url);
      if (res.status !== 200) throw await res.text();
      const items = await res.json();
      actions.initLoadItems(items);
      // return toast.success('ðŸš€ Triggers loaded successfully!', toastConfig);
    } catch (err) {
      toast.error(` ðŸ¤¦ Error while loading triggers ${err}!`, toastConfig);
    }
  }),
  saveItem: thunk(async (actions, payload) => {
    try {
      //Validation
      if (!payload.qty)
        return toast.error(` ðŸ¤¦ Quantity cannot be empty!`, toastConfig);
      const url = '/items';
      const params = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      };
      const res = await fetch(url, params);
      if (res.status !== 200) throw await res.text();
      //Fill in id_str for new recs
      const rec = payload.id_str ? payload : await res.json();
      actions.updateItem(rec);
      return toast.success('ðŸš€ Trigger saved successfully!', toastConfig);
    } catch (err) {
      toast.error(` ðŸ¤¦ Error while saving trigger ${err}!`, toastConfig);
    }
  }),
  deleteItem: thunk(async (actions, payload) => {
    try {
      //Confirmation promt
      const willDelete = await swal({
        title: 'Are you sure?',
        text: 'Are you sure that you want to delete this trigger ?',
        icon: 'warning',
        dangerMode: true,
      });
      if (!willDelete) return;
      const url = `/items/${payload}`;
      const params = {
        method: 'DELETE',
      };
      const res = await fetch(url, params);
      if (res.status !== 200) throw await res.text();
      actions.deleteItemAction(payload);
      return toast.success('ðŸš€ Trigger deleted successfully!', toastConfig);
    } catch (err) {
      toast.error(` ðŸ¤¦ Error while deleting trigger ${err}!`, toastConfig);
    }
  }),
  //actions
  initLoadItems: action((state, payload) => {
    state.items = [...state.items, ...payload];
  }),
  deleteItemAction: action((state, payload) => {
    state.items = state.items.filter((d) => d.id !== payload);
  }),
  addNew: action((state, _) => {
    state.items = [initRow, ...state.items];
  }),
  updateItem: action((state, payload) => {
    state.items = state.items.map((d) => (d.id === payload.id ? payload : d));
    window.location.reload();
  }),
};
