import React from 'react';

const Header = () => {
  return (
    <header>
      <h1>
        <i className='fab fa-twitter'></i> Twitter Binance Bot
      </h1>
      <p>
        This bot allows you use tweets as a trigger to place orders on Binance
        exchange.
      </p>
    </header>
  );
};

export default Header;
